.header-screen {

	.center.flex-block {
		align-items: center;
		padding-top: 20px;
		padding-bottom: 20px;
	}
}

.header__logo_image {
	width: 270px;
	height: 140px;
	line-height: 140px;
	text-align: center;

	img {
		max-height: 140px;
		max-width: 230px;
		vertical-aLign: middle;
	}
}

.header_cart {
	display: block;
	height: 45px;
	line-height: 45px;
	border-radius: 50px;
	color: #ffffff;
	text-decoration: none;
	font-size: 17px;
	padding-left: 70px;
	position: relative;
	padding-right: 50px;
	box-sizing: border-box;
	width: 185px;

	&:hover {
		color: #ffffff;
	}

	&:hover:after {
		content: '';
		display: block;
		width: 100%;
		height: 100%;
		border-radius: 50px;
		left: 0;
		top: 0;
		position: absolute;
		z-index: 2;
		background: rgba(0,0,0,.08);
	}

	b {
		font-weight: normal;
		position: relative;
		z-index: 5;
	}

	&:before {
		content: '';
		display: block;
		background:  url(#{$image-url}cart.png) center center no-repeat;
		height: 28px;
		width: 32px;
		position: absolute;
		left: 20px;
		top: 9px;
		z-index: 5;
	}

	span {
		display: block;
		width: 30px;
		height: 30px;
		border-radius: 50px;
		background: #c80d0d;
		line-height: 30px;
		text-align: center;
		position: absolute;
		top: 7px;
		right: 8px;
		z-index: 5;
	}
}

.color-red .header_cart span {
	background: #680606;
}

.header__contacts {
	margin-bottom: 7px;
	.phones {
		a {
			color: #000000;
			font-size: 24px;
			text-decoration: none;
			padding-left: 25px;
			background:  url(#{$image-url}phone.png) left center no-repeat;
			margin-right: 20px;
		}
	}
	.socials {
		width: 185px;
	}
}
.header-content {
	width: unquote("calc(100% - 290px)"); 
}
.show-mobile-phone {
	display: none;
}

.header_search {
	position: relative;
}

.header_search form {
	width: unquote("calc(100% - 205px)");
	height: 45px;
	border: 1px solid #c8c7c7;
	margin-right: 20px;
	border-radius: 50px;
	background: #ffffff;
	overflow: hidden;
	position: relative;

	/*.invisible-overlay {
		position: absolute;
		z-index: 5;
		width: 100%;
		height: 100%;
		background: transparent;
		left: 0;
		top: 0;
	}*/

	input[type="text"] {
		border-radius: 60px 0 0 60px;
		font-family: 'Source Sans Pro', sans-serif;
		font-size: 16px;
		padding-left: 20px;
		height: 45px;
		line-height: 43px;
		width: unquote("calc(100% - 50px)"); 
	}
	.search_button {
		height: 45px;
		width: 45px;
		border: none;
		outline: none;
		background: url(#{$image-url}lupa.png) center center no-repeat;
		border-radius: 0 50px 50px 0;
		cursor: pointer;
		vertical-align: top;
	}
}
.close-mobile-phone {
	display: none;
}

.header-screen {
	@include media-1000 {
		height: 50px;
		background: #ffffff;
		box-shadow: 0 5px 10px rgba(0,0,0,.3);
		position: fixed;
		z-index: 49;
		width: 100%;
		left: 0;
		top: 0;

		.header__logo_image {
			height: 40px;
			line-height: 40px;
			text-align: left;
			position: absolute;
			top: 5px;
			left: 10px;
	
			img {
				max-height: 40px;
				max-width: 100%;
			}
		}
	
		.header_cart  {
			position: fixed;
			right: 210px;
			top: 8px;
			z-index: 50;
			width: 35px;
			padding: 0px;
			height: 35px;
	
			&:before {
				width: 35px;
				height: 35px;
				background-size: 22px auto;
				left: 0;
				top: 0;
				border-radius: 50px;
			}
	
			span {
				width: 20px;
				height: 20px;
				line-height: 20px;
				top: 0;
				right: -12px;
				font-size: 12px;
			}
	
			b {
				display: none;
			}
		}
		.header_search form.header_search_form {
			display: none;
			position: fixed;
			height: 70px;
			background: var(--main-500);
			left: 0;
			top: 50px;
			z-index: 50;
			padding: 0 10px;
			width: 100%;
			box-sizing: border-box;
			box-shadow: 0 20px 20px rgba(0,0,0,.5);
			width: 100%;
			margin-right: 0;
			padding-top: 12px;
			border: 0;
			border-radius: 0;
			.search_button {
				border-radius: 0 50px 50px 0;
				background-color: #ffffff;
			}
		}
		.header__contacts {
			position: fixed;
			z-index: 50;
			right: 0;
			top: 0;
	
			.phones {
				display: none;
				position: fixed;
				top: 50px;
				border-top: 1px solid var(--main-500);
				right: 0;
				background: #ffffff;
				padding: 10px 20px 10px 20px;
				width: 250px;
				box-shadow: 0 20px 20px rgba(0,0,0,.5);
	
				a {
					display: block;
					padding-top: 10px;
					padding-bottom: 10px;
					width: 200px;
				}
			}
		}
		.header__contacts .socials {
			width: 110px;
			height: 35px;
			padding-top: 9px;
			a {
				margin-left: 0px !important;
				margin-right: 2px !important;
			}
		}
	
		.show-mobile-phone {
			width: 30px;
			height: 30px;
			position: fixed;
			top: 8px;
			right: 120px;
			background: url(#{$image-url}phone.png) center center no-repeat;
			background-size: 16px auto;
			display: block;
			border-radius: 50px;
			border: 2px solid var(--light-500);
			cursor: pointer;
		}
	
		.show-mobile-search {
			width: 30px;
			height: 30px;
			position: fixed;
			top: 8px;
			right: 160px;
			background: url(#{$image-url}lupa.png) center center no-repeat;
			background-size: 16px auto;
			display: block;
			border-radius: 50px;
			border: 2px solid var(--light-500);
			cursor: pointer;
		}
	
		.close-mobile-phone {
				display: block;
				font-size: 46px;
				font-weight: 500;
				position: absolute;
				right: 15px;
				top: 15px;
				width: 20px;
				opacity: .4;
				color: #2d4859;
				cursor: pointer;
	
				&:hover {
					opacity: 1;
				}
			}
	}
	
	@include media-900 {
		.header__logo_image {
			width: 200px;
	
			img {
				max-width: 100%;
			}
		}
		.header-content {
			width: unquote("calc(100% - 220px)"); 
		}
		.header__contacts .phones a {
			font-size: 20px;
		}
	}
	
	@include media-480 {
		.header__logo_image {
			width: 120px;
		}
		.header__contacts .phones {
			box-sizing: border-box;
			width: 100%;
		}
	}
}

// header popups
.header-filters {  
	&__popup {
	  max-width: 420px;
	  z-index: 99;
	  visibility: hidden;
	  opacity: 0;
	  transition: .3s;
	  cursor: default;
	  position: absolute;
	  width: 420px;
	  background: #fff;
	  top: 128%;
	  border: 1px solid #E1E3E6;
	  border-radius: 5px;
	  box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.1);
	  right: auto;
	  left: 0;
	  display: flex;
	  flex-direction: column;
	  padding: 17px 20px 24px;
	  text-align: center;
	  color: $black;  
	  transform: translateX(-18%);
  
	  &::before {
		content: '';
		position: absolute;
		right: auto;
		left: 50%;
		top: -6px;
		width: 12px;
		height: 12px;
		background-color: $white;
		transform: translateX(-50%) rotate(45deg);
		border: 1px solid #E1E3E6; 
	  }
  
	  &::after {
		content: '';
		position: absolute;
		right: auto;
		left: 50%;
		top: 0px;
		width: 20px;
		height: 12px;
		background-color: $white;
		transform: translateX(-50%);
	  }
  
	  p {
		margin: 0 0 16px;
		font-size: 16px;
		line-height: 18px;
	  }
  
	  .icon-circle {
		width: 68px;
		height: 68px;
		margin-bottom: 16px;
		position: relative;
  
		.icon {
		  width: 35px;
		  height: 27px;
		  margin: 0;
		}
	  }
  
	  &.active {
		visibility: visible;
		opacity: 1;

		@include media-1000 {
			visibility: hidden;
			opacity: 0;
		}

	  }
  
	  &-title {
		font-size: 18px;
		line-height: 1.2;
		font-weight: 600;
		margin-bottom: 16px;
	  }
  
	  .link {
		cursor: pointer;
		text-decoration: underline;
		font-size: 16px;
		font-weight: bold;
		color: #026CBF;
		
		&:not(:last-of-type) {
		  margin-bottom: 16px;
		}
	  }
	}
  } 
  

  // header cart popup
  .header__cart {  
	&-popup {
	  z-index: 99;
	  visibility: hidden;
	  opacity: 0;
	  transition: .3s;
	  cursor: default;
	  position: absolute;
	  width: 420px;
	  background: #fff;
	  top: 130%;
	  border: 1px solid #E1E3E6;
	  border-radius: 5px;
	  box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.1);
	  right: 0;
	  display: flex;
	  flex-direction: column;
	  padding: 17px 20px 24px;
	  text-align: center;
	  color: $black;
	  transform: translateX(0);
  
	  &::before {
		content: '';
		position: absolute;
		top: -6px;
		right: 60px;
		width: 12px;
		height: 12px;
		background-color: $white;
		transform: rotate(45deg);
		border: 1px solid #E1E3E6;

		@include breakpoint(xl) {
			right: 60px;
			left: auto
		}
	  }
  
	  &::after {
		content: '';
		position: absolute;
		top: 0px;
		right: 45px;
		width: 20px;
		height: 12px;
		background-color: $white;

		@include breakpoint(xl) {
			right: 48px;
			left: auto
		}
	  }
  
	  p {
		margin: 0 0 16px;
		line-height: 18px;
		font-size: 16px;
	  }
  
	  .icon-circle {
		width: 68px;
		height: 68px;
		margin-bottom: 16px;
		position: relative;
  
		.icon-circle__count {
		  position: absolute;
		  top: 0;
		  right: 0;
		  width: 17px;
		  height: 17px;
		  color: $white;
		  @include fontSize(10px);
		  font-weight: bold;
		  border-radius: 100%;
		  display: flex;
		  align-items: center;
		  justify-content: center;
		  background-color: var(--button-primary);
		}
  
		.icon {
		  width: 30px;
		  height: 32px;
		  margin: 0;
		}

		@include breakpoint(xl) {
			right: 0;
			left: auto;
			transform: translateX(0);
			width: 68px;
	  
			&::before {
			  right: auto;
			  left: 60px;
			  transform: translateX(-50%) rotate(45deg);
			}
	  
			&::after {
			  right: 48px;
			  left: auto;
			  transform: translateX(-50%);
			}
		  }
	  }
  
	  &.active {
		visibility: visible;
		opacity: 1;

		@include media-1200 {
			transform: translateX(-65px);
		}

		@include media-1000 {
			visibility: hidden;
			opacity: 0;
		}
	  }
  
  
	  .link {
		font-size: 16px;
	  }	  
	}
  }
  