.map__block {
	position: relative;
	overflow: hidden;
	img {
		display: block;
	}

	.center {
		position: absolute;
		top: 0;
		left: 50%;
		margin-left: -600px;
		width: 1200px;
		height: 100%;
		padding: 0;
	}	
}
.map__contacts {
		position: absolute;
		top: 45px;
		right: 50px;
		background: #ffffff;
		box-shadow: 0 20px 40px rgba(0,0,0, .3);
		width: 320px;
		box-sizing: border-box;
		padding: 30px 30px 20px 30px;

		p {
			text-transform: uppercase;
			text-align: center;			
			font-size: 24px;
			margin-bottom: 20px;
			font-weight: 600;
		}

		.phone  {
			padding-left: 50px;
			background: url(#{$image-url}phone.png) 17px 3px no-repeat;
			margin-bottom: 20px;

			a {
				color: #000000;
				font-weight: 600;
				font-size: 24px;
				text-decoration: none;
                display: block;
                margin-bottom: 5px;
			}
		}

		.geo {
			padding-left: 50px;
			background: url(#{$image-url}geo.png) 20px top no-repeat;
			margin-bottom: 20px;
		}

		.socials {
			padding-top: 20px;
			border-top: 1px solid rgba(0,0,0,.2);

			a {
				margin: 0 8px;
			}

            .wa_line, .viber_line {
                stroke: var(--main-500);
                fill: var(--main-500);
		    }
            .telegram_line {
                fill: var(--main-500);
            }
        }
}

.map__block {
	@include media-1200 {
		.center {
			width: 100%;
			left: 0;
			margin-left: 0;
		}
    }

	@include media-780 {
		.center {
			position: relative;
			left: auto;
			top: auto;
			background: var(--light-500);
			padding: 10px 0;
		}
		.map__contacts {
			position: relative;
			margin-left: auto;
			margin-right: auto;
			top: auto;
			right: auto;
		}
    }
}

