.locked-body {
    overflow: hidden;

    .multiselect_form {
        z-index: 1050;
    }
    .header-screen {
        position: relative;
        z-index: 1040;

        @include media-1000 {
            position: fixed;
        }
    }
}
.search-screen {
    &__overlay {
        background: #ffffff;
        position: fixed;
        top: 248px;
        left: 0;
        width: 100%;
        height: calc(100% - 248px);
        z-index: 1000;
        display: none;
        box-shadow: 0 -5px 20px rgba(0,0,0,.1);

        &.behind {
            z-index: -1;
        }

        .center {
            position: relative;

            @include media-480 {
                padding-top: 40px;
            }
        }

        .search__result__none {
            font-size: 19px;
            font-weight: 600;
            margin-bottom: 30px;
        }

        @include media-1000 {
            top: 120px;
            height: calc(100% - 120px);
            z-index: 1100;
        }
    }
    

    &__close {
        position: absolute;
        right: 30px;
        top: 20px;
        font-size: 50px;
        cursor: pointer;
        opacity: .5;

        &:hover {
            opacity: .8;
        }

        @include media-1000 {
            top: 10px;
            right: 10px;
        }
    }

    &__content {
        display: flex;
        justify-content: space-between;
        flex-direction: row-reverse;

        @include media-1000 {
            display: block;
        }
    }

    &__result-wrap {
        width: 48%;

        ul {
            font-size: 16px;
        }

        @include media-1000 {
            width: 100%;
        }
    }

    &__contact {
        width: 49%;
        font-size: 16px;
        line-height: 22px;
        color: #141414;

        @include media-1000 {
            display: none;
        }
    }

    &__contact-wrap {
        display: flex;
        align-items: center;
        margin-top: 25px;
        font-size: 15px;
        line-height: 18px;

        .icon {
            width: 37px;
            height: 42px;
            margin-right: 16px;
          }
    }

    &__contact-tel {
        font-size: 30px;
        font-weight: bold;
        color: #141414;
        text-decoration: none;
        display: inline-block;
    }

    &__result-title {
        font-size: 18px;
        font-weight: bold;
        margin-bottom: 10px;
    }
}
    