input[type='text'] {
	height: 40px;
	line-height: 40px;
	box-sizing: border-box;
	padding: 0 15px;
	width: 100%;
	color: #000000;
	border-radius: 6px;
	border: none;
	font-family: 'Source Sans Pro', sans-serif;
	font-size: 16px;

	&.error {
		color: #c21010;
		background: #fce6e6;
	}
}