.button_big {
	height: 50px;
	border-radius: 8px;
	padding: 0 45px;
	box-sizing: border-box;
	color: #ffffff;
	font-family: 'Source Sans Pro', sans-serif;
	text-transform: uppercase;
	box-shadow: 0 10px 20px rgba(0,0,0,.4);
	border: none;
	outline: none;
	background: #ee0f0f;
	font-size: 18px;
	font-weight: 600;
	text-transform: uppercase;
	cursor: pointer;
	text-align: center;

	&:hover {
		background: #d50808;
	}

	@include media-1100 {
		height: 45px;
		padding: 0 30px;
		font-size: 16px;
	}

	@include media-900 {
		height: 40px;
	}
}