.multiselect_form {
	position: relative;
	z-index: 50;

	&:before {
		content: '';
		display: block;
		width: 100%;
		height: 100%;
		left: 0;
		top: 0;
		background: linear-gradient(rgba(0,0,0,.4),rgba(0,0,0,.2));
		position: absolute;
		z-index: 2;
	}	

	.center {
		position: relative;
		z-index: 5;
		padding-top: 15px;
		padding-bottom: 15px;
		color: #ffffff;
	}

	span {
		width: 15%;
	}

	form {
		display: flex;
		justify-content: space-between;
		width: 85%;
		position: relative;
	}

	.flex-block {
		align-items: center;
	}

	.button_big {
		height: 38px;
		box-shadow: none;
		width: 20%;
		font-size: 16px;
		text-transform: none;
		border-radius: 4px;
		margin-left: 20px;
	}

	select {
		margin-left: 15px;
		height: 38px;
		border-radius: 4px;
		border: none;
		outline: none;
		background: #ffffff;
		width: 25%;
		box-sizing: border-box;
		padding: 0 12px;
		font-size: 15px;
	}
	.close-multiselect {
		display: none;
	}
}

.multiselect_form {

	@include media-1100 {
		span {
			font-size: 16px;
		}

		.center {
			padding-top: 10px;
			padding-bottom: 10px;
		}
	}

	@include media-1000 {
		position: fixed;
		width: 100%;
		height: auto;
		left: 0;
		top: 120px;
		z-index: 50;
		padding: 20px 0 10px 0;
		display: none;
		box-shadow: 0 20px 20px rgba(0,0,0,.5);

		span {
			display: block;
			padding-bottom: 10px;
			font-size: 18px;
			width: 100%;
		}

		.flex-block {
			display: block;
			position: relative;

			form {
				display: block;
				padding: 10px 0;
				box-sizing: border-box;
				width: 100%;
			}
		}

		select {
			width: 100%;
			margin-bottom: 20px;
			margin-left: 0;
		}

		.button_big {
			width: 250px;
			margin-left: auto;
			margin-right: auto;
			display: block;
		}

		span.close-multiselect {
			display: block;
			font-size: 56px;
			font-weight: 500;
			position: absolute;
			right: 0;
			top: 0;
			width: 40px;
			opacity: .4;
			color: #ffffff;
			cursor: pointer;

			&:hover {
				opacity: 1;
			}
		}		
	}
}