.profits__item__blank {
	position: absolute;
	background: #ffffff;
	width: 100%;
	height: 100%;
	bottom: 0;
	right: 0;
	transition: all .8s linear;
	z-index: 10;
	transition-delay: calc(0.3s * var(--i));
}

.animated {
	.profits__item__blank {
		height: 0;
		width: 0;
	}
}

.profits__content.flex-block {
	flex-wrap: wrap;
}
.profits__item {
	width: 30%;
	box-sizing: border-box;
	position: relative;
	overflow: hidden;
	background: #ffffff;
	box-shadow: 0 0 20px rgba(0,0,0,.3);
	margin-bottom: 60px;

	&:hover .profits__item__hover {		
		opacity: 1;
	}	
}
.profits__item__hover {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	color: #ffffff;
	box-sizing: border-box;
	z-index: 5;	
	transition: all .2s linear;	
	display: flex;
	justify-content: center;
	align-items: center;
	overflow: hidden;
	opacity: 0;
	text-align: center;

	p {
		font-size: 17px; 
		line-height: 24px;
		padding: 20px 40px;	

		@include media-1100 {
			font-size: 16px; 
			line-height: 20px;
		}

	}
	&:before {
		content: '';
		display: block;
		width: 75px;
		height: 75px;
		background-position: center center;
		background-repeat: no-repeat;
		position: absolute;
		top: 20px;
		left: 20px;
		opacity: .1;
		filter: invert(100%);
	}

	&.pic_01:before {background-image: url(#{$image-url}profit_01.png);}
	&.pic_02:before {background-image: url(#{$image-url}profit_02.png);}
	&.pic_03:before {background-image: url(#{$image-url}profit_03.png);}
	&.pic_04:before {background-image: url(#{$image-url}profit_04.png);}
	&.pic_05:before {background-image: url(#{$image-url}profit_05.png);}
	&.pic_06:before {background-image: url(#{$image-url}profit_06.png);}
}
.profits__item__content {
		padding: 50px 0 50px 0;

	p {
		font-size: 20px;
		margin-bottom: 35px;
		padding: 0 20px 0 115px;

		b {
			font-size: 24px;
			font-weight: 400;
			padding-top: 3px;
			display: block;
		}

		@include media-1100 {
			font-size: 18px;
			b {
				font-size: 20px;
			}
		}
	}

	div {
		height: 2px;
		width: 100%;
		border-top: 2px solid #e2e2e2;
		position: relative;

		b {
			display: block;
			position: absolute;
			color: #ffffff;
			width: 38px;
			height: 38px;
			border-radius: 50px;
			border: 4px solid #ffffff;
			right: 20px;
			top: -19px;
			text-align: center;
			line-height: 27px;
		}
	}

	span {
		display: block;
		width: 75px;
		height: 75px;
		background-position: center center;
		background-repeat: no-repeat;
		position: absolute;
		top: 20px;
		left: 20px;
		opacity: .2;
	}
	&.pic_01 span {background-image: url(#{$image-url}profit_01.png);}
	&.pic_02 span {background-image: url(#{$image-url}profit_02.png);}
	&.pic_03 span {background-image: url(#{$image-url}profit_03.png);}
	&.pic_04 span {background-image: url(#{$image-url}profit_04.png);}
	&.pic_05 span {background-image: url(#{$image-url}profit_05.png);}
	&.pic_06 span {background-image: url(#{$image-url}profit_06.png);}
}

.profits__block.light-block {
	background: #f7f7f7;
	
}
/* profit-screen // color block */

.profits__block.color-block {
	position: relative;

	&:before {
		content: '';
		height: 100%;
		width: 100%;
		position: absolute;
		left: 0;
		top: 0;
		background: linear-gradient(to bottom, rgba(0,0,0,.5), rgba(0,0,0,0));
		z-index: 3;
	}
	.center {
		position: relative;
		z-index: 5;
	}
	.subtitle {
		color: #ffffff;
		opacity: .7;
	}
}
.color-block .profits__item__hover.color-block{
	&:after {
		content: '';
		height: 100%;
		width: 100%;
		position: absolute;
		left: 0;
		top: 0;
		background: #ffffff;
		z-index: 3;
		opacity: .6;
	}

	&:before {
		opacity: .4;
	}

	p {
		position: relative;
		z-index: 5;
	}
}

.vertical {
	.profits__item__content {
		padding: 25px 0;

		p {
			padding-left: 120px;
			margin-bottom: 5px;
		}

		&:before {
			display: none;
		}

		&:after {
			border-radius: 0;
			width: 70px;	
			height: 100%;
			left: 0;
			top: 0;	
		}
	}
	.profits__item__content span {
		display: block;
		position: relative;
		left: auto;
		top: auto;
		margin-left: 120px;
		margin-bottom: 20px;

		@include media-1100 {
			margin-bottom: 10px;
		}
	}

	.profits__item__content div {
		position: absolute;
		left: 25px;
		top: 15px;
		width: 60px;
		height: 60px;
		border: none;

		b {
			width: 100%;
			height: 100%;			
			border-radius: 50px;
			top: 0;
			left: 0;
			line-height: 58px;
			font-size: 30px;
			font-weight: 600;
			color: rgba(255,255,255,.7);
			border: 0;
		}
	}
}

.profits__block {
	@include media-900 {
		.profits__item {
			width: 32%;
			margin-bottom: 20px;
		}

		&.vertical {
			.profits__item__content p {
				padding-left: 80px;
			}
			.profits__item__content span {
				margin-left: 80px;
			}
			.profits__item__content:after {
				width: 50px;
			}
			.profits__item__content div {
				width: 45px;
				height: 45px;
				left: 15px;
				b {
					line-height: 43px;
				}
			}
		}		 
	}

	@include media-780 {
		.profits__item {
			width: 49%;
		}
	}

	@include media-600 {
		.profits__item__content {
			padding: 20px 0 20px 0;
		}
		.profits__item__content p {
			margin-bottom: 20px;
			padding-left: 80px;
		}
		.profits__item__content span, .profits__item__hover::before {
			width: 60px;
			height: 60px;
			background-size: 80% auto;
			top: 10px;
			left: 10px;
		}
		.profits__item__content.pic_05 span, 
		.profits__item:nth-child(5) .profits__item__hover::before {
			background-size: 65% auto;
		}
	}

	@include media-480 {
		.profits__item {
			width: 100%;
			margin-bottom: 10px;
		}

		&.vertical {
			.profits__item__content {
				display: flex;
				align-items: center;
				padding: 15px 0;
				p {
					padding-left: 20px;
					padding-right: 10px;
				}
				span {
					margin-left: 70px;
					width: 60px;
					height: 70px;
					background-size: 90% auto;
				}
			}
			.profits__item__content.pic_01 span {background-size: 80% auto;}
			.profits__item__content.pic_02 span {background-size: 95% auto;}
		
			.profits__item__content:after {
				width: 40px;
			}
			.profits__item__content div {
				width: 35px;
				height: 35px;
		
				b {
					line-height: 33px;
					font-size: 24px;
				}
			}
		}
	}
}