.order-banner-form {
    * {
        font-family: 'Source Sans Pro', sans-serif;
    }
    width: 100%;
    padding: 30px;
    border-radius: 10px;
    position: relative;
    margin-top: 60px;
    @include media-600 {
       padding: 20px;
       margin-top: 30px;
    }

    &:before {
        content: '';
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 2;
        left: 0;
        top: 0;
        border-radius: 10px;
        background: linear-gradient(to top, rgba(0,0,0,0),rgba(0,0,0,.3));
    }

    .order-banner__text {
        color: #ffffff;
        padding-right: 20px;
        position: relative;
        z-index: 4;

        @include media-1200 {
            padding-right: 0;
        }
    }

    form {        
        display: flex;
        align-items: flex-start;
        flex-wrap: wrap;
        justify-content: space-between;
        position: relative;
        z-index: 4;

        .form-block_01, .form-block_02 {
            width: 60%;
            @include media-1200 {
                width: 100%;
            }
        }
        .form-block_02 {
            width: 35%;
            @include media-1200 {
                width: 100%;
            }
        }

        @include media-1200 {
            display: block;
        }

        .button-container {
            width: 49%;
            position: relative;
            z-index: 5;
            margin-top: -50px;

            @include media-1200 {
                margin-top: 0;
                padding-top: 20px;
                width: 100%;
                text-align: center;
            }
        }       
        textarea {
            height: 192px;
            @include media-1200 {
                height: 100px;
            }
        }
    }
}
.order-banner__title {
    font-size: 20px;
    margin-bottom: 5px;
}
.order-banner__subtitle {
    font-size: 18px;
    opacity: .6;
    margin-bottom: 25px;
}
.form-block_02 {
    .form-item {
        margin-bottom: 0;
    }
}

@media screen and  (min-width: 1px) and (max-width: 480px) {
    .manager-wrapper + .order-banner-form,
    .order-wrapper  + .order-banner-form {
        margin-top: 0;
    }
}