/* footer */
.footer-screen {
	position: relative;
	color: #ffffff;
}
.footer-screen:before {
	content: '';
	display: block;
	background: rgba(0,0,0,.25);
	width: 100%;
	height: 100%;
	position: absolute;
	left: 0;
	top: 0;
	z-index: 2;
}
.footer-screen {
	.center {
		position: relative;
		z-index: 4;
		padding: 40px 30px;
	}

	b {
		font-size: 18px;
		font-weight: normal;
	}

	a {
		color: #ffffff;
		text-decoration: none;
		font-size: 20px;	
		margin-left: 20px;	
	}

	.footer__mail a {
		font-size: 16px;
		padding-left: 35px;
		background: url(#{$image-url}mail.png) left center no-repeat;
	}
	.socials {
		margin-left: 30px;
		margin-bottom: 5px;
	}
	.center {
		position: relative;
		z-index: 4;
		padding: 40px 30px;
	}
}
.footer__copy_right {
	span {
		padding-right: 10px;
	}
}
.footer__info {
	text-align: center;
	padding: 30px 0 0 0;
	font-size: 17px;
	border-top: 1px solid rgba(255,255,255,.5);

	p {
		margin-bottom: 5px;

		&:last-child {
			margin-bottom: 0;
		}
	}
	
}

.footer-screen {
	@include media-900 {
        .footer__content.flex-block {
			display: block;
			div {
				margin-bottom: 10px;
			}
			.socials {
				justify-content: flex-start;
			}
		}
	
		a {
			margin-left: 0;
			margin-right: 20px;
		}
		.socials {
			margin-left: 0;
		}
		.footer__info {
			font-size: 15px;
			line-height: 18px;
			text-align: left;
		}
		.footer__phone a {
			display: block;
			margin-bottom: 10px;
		}
    }
}
