.catalog_button {
    display: block;
    height: 45px;
    line-height: 45px;
    margin-right: 20px;
    padding-left: 70px;
    padding-right: 25px;
    position: relative;
    color: #000000;
    cursor: pointer;
    text-decoration: none;
    border-radius: 6px;
    overflow: hidden;
    background: #efb7b7;

    span {
        position: relative;
        z-index: 5;
    }

    b {
        position: absolute;
        left: 0;
        top: 0;
        height: 45px;
        border-radius: 6px 0 0 6px;
        width: 55px;
        transition: all .3s linear;
        z-index: 2;
        background: #c80d0d  url(#{$image-url}catalog-white.png) 12px 7px no-repeat;
    }
    &:hover {
        color: #ffffff;
        b {
            width: 100%;
        }
    }
}