.main {
    padding-top: 0;
    margin-top: 0;
    font-size: 12px;
    line-height: 1;
}

.container {
    @include media-1200 {
        max-width: 100%;
        padding: 0 20px;
    }

}

.row {
    @include media-1200 {
        max-width: 100%;
        padding: 0;
    }

}

.title {
    @include breakpoint(xl) {
        font-size: 1.9rem;
    }
}
.subtitle {
    font-size: 1.4rem;
}
.form__order-title {
    font-size: 1.7rem;
    font-weight: 600;
    margin-bottom: 12px;
}
.form__order__item {
    font-size: 1.4rem;
}
.title {
    font-size: 2.3rem;
    font-weight: 400;
    margin-bottom: 20px;
    line-height: 34px;
    width: 100%;

    @include media-480 {
        font-size: 2rem;
        line-height: 1.2;
    }
  }

  .form__group {
    input[type="text"] {
        border: 1px solid #E1E3E6;
    }
    .form__label {
        font-size: 14px;
    }
  }