html {
    font-size: 10px;
}
body {
	font-family: 'Source Sans Pro', sans-serif;
	font-size: 18px;
	line-height: 24px;

	@include media-1100 {
        font-size: 16px;
		line-height: 20px;
    }
	@include media-1000 {
		padding-top: 50px;
	}
}

.main_screen {
	h1 {
		font-size: 4.8rem;
		line-height: 48px;
		color: #ffffff;
		margin-bottom: 55px;
		font-weight: 600;

		@include media-1100 {
			font-size: 40px;
			line-height: 40px;
			margin-bottom: 40px;

			span {
				font-size: 30px;
			}
		}
		@include media-480 {
			font-size: 30px;
			line-height: 30px;
			margin-bottom: 20px;

			span {
				font-size: 24px;
			}
		}
	}

	h2 {
		font-size: 30px;
		color: #ffffff;
		margin-bottom: 80px;
		font-weight: 600;
	
		@include media-1100 {
			font-size: 24px;
			margin-bottom: 60px;
		}	
	
		@include media-480 {
			font-size: 20px;
			line-height: 24px;
			margin-bottom: 30px;
		}
	}

	h1 span {
		font-size: 36px;
		font-weight: 400;
		display: block;
	}
}

.center h3 {
	font-size: 40px;
	line-height: 54px;
	color: var(--main-500);
	margin-bottom: 30px;
	font-weight: 600;
	text-align: center;

	@include media-1100 {
		font-size: 36px;
		line-height: 40px;
	}

	@include media-900 {
		font-size: 30px;
		line-height: 30px;
		margin-bottom: 10px;
	}

	@include media-480 {
		font-size: 20px;
		line-height: 24px;
		margin-bottom: 30px;
	}
}

.main-subtitle {
	font-size: 24px;
	line-height: 30px;
	margin-bottom: 80px;
	text-align: center;

	@include media-1100 {
		font-size: 20px;
		margin-bottom: 60px;
	}

	@include media-900 {
		margin-bottom: 30px;
		font-size: 18px;
	}

	@include media-480 {
		font-size: 16px;
		line-height: 20px;
	}
}
.color-block h3 {
	color: #ffffff;
}

.main * {
	font-family: 'Roboto', sans-serif;
}