.main_screen {
	background: url(#{$image-url}main-bg.jpg) center top no-repeat;
	background-attachment: fixed;

	.main_screen__text {
		padding-top: 60px;
	}
	.center {
		overflow: hidden;
	}

	h1 {
		margin-left: -3000px;		
		transition: all .8s linear;
		transition-delay: 1s;
	}
	h2 {
		margin-left: -3000px;
		transition: all 1s linear;
		transition-delay: 1.5s;
	}
	.main_screen__form {
		opacity: 0;
		transition: all 1s linear;
		transition-delay: 3s;
	}

	@include media-1100 {
		.main_screen__form p {
			font-size: 20px;
		}
	}
}

.main_screen.start {
	h1 {
		margin-left: 0;
	}
	h2 {
		margin-left: 0;
	}
	.main_screen__form {
		opacity: 1;
	}
	.main_screen__buttons {
		margin-left: 0;
	}
}

.main_screen__buttons {
	margin-left: -3000px;
	transition: all 1s linear;
	transition-delay: 2s;
	h2 {
		padding-top: 10px;
	}
	padding-top: 10px;
	a {
		display: block;
		height: 45px;
		line-height: 45px;
		margin-right: 20px;
		padding-left: 70px;
		padding-right: 25px;
		position: relative;
		color: #000000;
		cursor: pointer;
		text-decoration: none;
		border-radius: 6px;
		overflow: hidden;

		span {
			position: relative;
			z-index: 5;
		}

		b {
			position: absolute;
			left: 0;
			top: 0;
			height: 45px;
			border-radius: 6px 0 0 6px;
			width: 55px;
			transition: all .3s linear;
			z-index: 2;
		}

		&.main_screen__wa {
			background: #b6e4b6;

			b {
				background: #0aa40a  url(#{$image-url}wa-white.png) 12px 7px no-repeat;
			}

		}
		&.main_screen__catalog {
			background: #efb7b7;
			b {
				background: #c80d0d  url(#{$image-url}catalog-white.png) 12px 7px no-repeat;
			}
		}

		&:hover {
			color: #ffffff;
			b {
				width: 100%;
			}
		}
	}

	&.flex-block {
		justify-content: flex-start;
	}
}
/* main screen form */
.main_screen__form {
	border-radius: 8px;
	background: rgba(255,255,255,.15);
	box-sizing: border-box;
	width: 400px;
	padding: 25px 35px 35px 35px;
	box-shadow: 0 0 20px rgba(0,0,0,.2);

	.button {
		margin-left: auto;
		margin-right: auto;
		width: 70%;
		display: block;
	}
	p {
		text-align: center;
		color: #ffffff;
		font-size: 24px;
		margin-bottom: 20px;
	}

}

.main_screen {
	@include media-900 {
		.center.flex-block {
			display: block;
		}
		.main_screen__text {
			padding-top: 10px;
	
			h1 {
				margin-bottom: 30px;
			}
			h2 {
				margin-bottom: 20px;
			}
		}
		&.start {
			.main_screen__buttons {
				margin-bottom: 30px;
			}
		}
		.main_screen__form {
			margin-left: auto;
			margin-right: auto;
			width: 100%;
			max-width: 500px;
		}
	}

	@include media-780 {
		.main_screen__buttons.flex-block {
			display: block;
	
			a {
				display: block;
				margin-bottom: 15px;
				width: 70%;
				box-sizing: border-box;
			}
		}
		h1, 
		h2, 
		.main_screen__buttons {
			margin-left: 0 !important;
		}
		.main_screen__form {
			opacity: 1 !important;
		}
	}

	@include media-600 {
		.main_screen__form {
			padding: 20px 20px 25px 20px;
		}
	}

	@include media-480 {
		.main_screen__buttons.flex-block {
			a {
				width: 100%;
				margin-right: 0;
			}
		}
	}
}
