.tab {
  width: 100%;
  margin-top: 20px;

  @include media-1000 {
    margin-top: 0;
  }

  &__link {
    border-bottom: 1px solid #E1E3E6;
    display: flex;

    @include breakpoint(xl) {
      overflow-x: auto;
      margin: 0 -15px;
      width: calc(100% + 30px);
    }

    @include media-1000 {
      display: none;
    }
  }

  &__name {
    cursor: pointer;
    position: relative;
    font-weight: bold;
    @include fontSize(15px);
    padding-bottom: 10px;
    white-space: nowrap;

    &:not(:first-of-type) {
      margin-left: 30px;
    }

    &:first-of-type {
      @include breakpoint(xl) {
        padding-left: 15px;
      }
    }

    &:last-of-type {
      @include breakpoint(xl) {
        padding-right: 15px;
      }
    }

    &.active {
      color: var(--button-primary);

      &:before {
        content: '';
        position: absolute;
        bottom: -2px;
        width: 100%;
        height: 2px;
        background: var(--button-primary);
        @include breakpoint(xl) {
          bottom: 0;
        }
      }
    }
  }

  &__item {
    display: none;

    &.active {
      display: block;
    }

    @include media-1000 {
      display: block;

      .link-list, .link-list-title {
        display: none;
      }
    }
  }
  
  &__content {
    margin-top: 24px;

    @include media-1000 {
      margin-top: 0;
    }
  }
}

.mobile__tab__name {
  display: none;

  &:after {
    content: '';
    position: absolute;
    right: 20px;
    top: 15px;
    border: 6px solid transparent;
    border-top: 6px solid #808080;
  }

  @include media-1000 {
    display: block;
    @include fontSize(16px);
    padding: 10px 20px;
    border-bottom: 1px solid #dedede;
    margin-bottom: 10px;    
    position: relative;
    cursor: pointer;
    margin-left: -20px;
    margin-right: -20px;
  }
}