.form-item  {
    .checkbox-label {
        font-size: 14px;		

        span {
            padding-left: 30px;
            position: relative;

            &:before {
                content: '';
                display: block;
                height: 20px;
                width: 20px;
                border-radius: 3px;
                background: #ffffff;
                position: absolute;
                left: 0;
                top: -2px;
            }
        }

        input {
            display: none;
        }

        input:checked + span:after {
            content: '';
            display: block;
            height: 13px;
            width: 7px;
            border-radius: 3px;
            background: transparent;
            position: absolute;
            left: 5px;
            top: -1px;
            transform: rotate(45deg);
            border-right: 3px solid var(--main-500);
            border-bottom: 3px solid var(--main-500);
        }
    }
}