.search-variants__slogan {
	text-align: center;
	color: #ffffff;
	font-size: 36px;
	font-weight: 600;
	position: relative;

	&:before {
		content: '';
		width: 100%;
		height: 100%;
		background: linear-gradient(to top, rgba(0,0,0,.3), rgba(0,0,0,0));
		position: absolute;
		left: 0;
		top: 0;
		z-index: 2;
	}

	.center {
		position: relative;
		z-index: 5;

		span {
			padding-left: 9px;
		}
	}

	@include media-1100 {
		font-size: 28px;

		.center {
			padding: 25px 30px;
		}
	}
}