* {
    box-sizing: border-box;
}
.flex-block {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
}

.center {
    width: 100%;
    max-width: 1200px;
    margin-right: auto;
    margin-left: auto;
    box-sizing: border-box;
    padding: 60px 30px;

    @include media-1100 {
        padding: 40px 30px;
    }

    @include media-480 {
        padding: 25px 20px;
    }
}

.form-item {
    margin-bottom: 25px;
    
    label {
        display: block;
        color: #ffffff;
        font-size: 16px;
        padding-bottom: 5px;
        padding-left: 5px;
    }
  }