.search-variants {
	background: url(#{$image-url}variants-bg.webp) center top no-repeat;
	background-attachment: fixed;
	position: relative;

	.color-cover {
		position: absolute;
		height: 100%;
		width: 100%;
		left: 0;
		top: 0;
		opacity: .2;
		z-index: 2;
	}

	.center {
		position: relative;
		z-index: 5;
	}

	h3 {
		margin-bottom: 60px;
	}
}
.search-variants__item {
	width: 47%;
	box-sizing: border-box;
	background: #ffffff;
	border-radius: 10px;
	margin-bottom: 20px;
	padding-bottom: 5px;
	opacity: 1;
	transition: all 1s linear;
	transition-delay: calc(.5s * var(--i));
	opacity: 0;
	position: relative;

	&:nth-child(2) {
		z-index: 2;
	}
}

.animated .search-variants__item {
	opacity: 1;
}
.search-variants__content {
	position: relative;
}

.search-variants__list {
	padding: 0 55px;
}
.search-variants__title {
	border-radius: 9px 9px 0 0;
	margin-bottom: 40px;
	padding: 20px 15px;
	padding-left: 80px;
	font-size: 22px;
	font-weight: 600;
	line-height: 24px;
	text-align: center;
	color: #ffffff;

	@include media-1100 {
		margin-bottom: 20px;
		padding: 10px 15px;		
		padding-left: 60px;
		font-size: 19px;
	}
}
.search-variants__number {
	display: block;
	width: 94px;
	height: 94px;
	border-radius: 50px;
	border: 7px solid #000000;
	position: absolute;
	left: 15px;
	top: -15px;
	text-align: center;
	line-height: 80px;
	font-size: 48px;
	font-weight: 600;
	color: #a6a6a6;
	background: #ffffff;
	z-index: 7;
}
.search-variants__list_item {
	margin-bottom: 30px;
	height: 50px;

	@include media-1100 {
		margin-bottom: 15px;
	}

	&.flex-block {
		justify-content: flex-start;
	}

	p {
		padding-left: 25px;
	}

	i {
		display: block;
		font-style: normal;
		opacity: .7;
		line-height: 20px;
		font-size: 17px;

		@include media-1100 {
			font-size: 14px;
			line-height: 17px;
		}
	}	
}
.search-variants__button {
	padding: 10px 55px 50px 55px;

	a {
		display: block;
		width: 100%;
		height: 60px;
		line-height: 60px;
		border-radius: 6px;
		text-align: center;
		color: #ffffff;
		text-decoration: none;
		background: #bf0303;
		font-size: 20px;
		text-transform: uppercase;
		font-weight: 600;
		overflow: hidden;
		position: relative;
		cursor: pointer;

		&:hover {
			background: #a51010;
		}

		b {
		  display: block;
		  height: 250%;
		  width: 150px;
		  background: linear-gradient(to right, rgba(255,255,255, 0), rgba(255,255,255, .3));
		  transform: skew(-35deg, -35deg);
		  position: absolute;
		  z-index: 5;
		  top: -30px;
		}
	}
}

/* animation button */
#blink {
  -webkit-animation: blink 3s 2s linear infinite;
  animation: blink 3s 2s linear infinite;
}
@-webkit-keyframes blink {
  0% {left: -200%;}
  100% { left: 450%;}
}
@keyframes blink {
  0% {left: -10%;}
  100% { left: 450%;}
}
.search-variants__form {
	position: absolute;
	box-sizing: border-box;
	width: 0;
	height: 0;
	position: absolute;
	left: 0;
	top: 0;
	opacity: 0;
	z-index: 6;
	border-radius: 10px;

	&:before {
		content: '';
		display: block;
		height: 100%;
		width: 100%;
		background: linear-gradient(to right, rgba(0,0,0,.3), rgba(0,0,0,0));
		position: absolute;
		left: 0;
		top: 0;
		z-index: 3;
		border-radius: 10px;
	}

	form {		
		padding: 55px 55px 45px 130px;
		position: relative;
		z-index: 5;

		p {
			font-size: 22px;
			color: #ffffff;
			margin-bottom: 30px;
		}

		textarea {
			height: 110px;
		}
	}

	.close-order-form {
		font-size: 56px;
		font-weight: 500;
		position: absolute;
		right: 20px;
		top: 20px;
		opacity: .4;
		color: #ffffff;
		cursor: pointer;

		&:hover {
			opacity: 1;
		}
	}
}

/* order-screen // light block */
.search-variants.light-block {
	.color-cover  {
		background: #ffffff;
		opacity: .85;
	}
	.search-variants__item {
		box-shadow: 0 0 20px rgba(0,0,0,.3);
	}
}

.color-block.light-block h3 {
	color: var(--main-500);
}

/* order-screen // horisontal */
.search-variants.horisontal  {
	.flex-block.search-variants-list {
		display: block;
	}
	.search-variants__item {
		width: 100%;
		padding-bottom: 30px;

		@include media-1100 {
			padding-bottom: 25px;
		}
	}
	.search-variants__list {
		display: flex;
		justify-content: space-between;
	}
	.search-variants__title {
		text-align: left;
	}
	.search-variants__number {
		color: #ffffff;
		background: none;
		border: none;
		width: 64px;
		height: 64px;
		line-height: 64px;
		opacity: .2;
		left: 10px;
		top: 0;

		@include media-1100 {
			width: 44px;
			height: 44px;
			line-height: 44px;
			font-size: 34px;
		}
	}
	.search-variants__list {
		padding: 0 40px;
	}
	.search-variants__list_item {
		height: auto;
		width: 24%;
		box-sizing: border-box;
		padding-right: 5px;
		p {
			padding-left: 10px;
			b {
				line-height: 20px;
				margin-bottom: 8px;
				display: block;

				@include media-1100 {
					line-height: 18px;
				}
			}
		}
	}
	.search-variants__button {
		padding: 0 40px;
		a {
			width: 50%;
			margin-left: auto;
			margin-right: auto;
			height: 50px;
			line-height: 50px;

			@include media-1100 {
				height: 45px;
				line-height: 45px;
			}
		}
	}
	.search-variants__item:nth-child(1) {
		margin-bottom: 50px;
	}

	.search-variants__form  {
		position: relative;
		left: auto;
		top: auto;
		width: 47% !important;
		margin-left: auto;
		margin-right: auto;

		form {
			padding: 25px 40px;
		}
	}
}

.search-variants {
	@include media-1200 {
		.search-variants__number {
			width: 70px;
			height: 70px;
			border-width: 6px;
			line-height: 58px;
			font-size: 36px;
		}		
	}
	@include media-1100 {
		.search-variants__number {
			width: 43px;
			height: 43px;
			border-width: 4px;
			line-height: 33px;
			left: 10px;
			top: -5px;
			font-size: 20px;
		}
		.search-variants__list {
			padding: 0 30px;
		}
		.search-variants__button {
			padding: 10px 30px 25px 30px;
		}
		.search-variants__form form {
			padding: 20px 30px 30px 65px;
		}
		.search-variants__form form p {
			margin-bottom: 20px;
		}
		.search-variants__form form textarea {
			height: 70px;
		}
	}

	@include media-900 {
		.flex-block.search-variants-list {
			display: block;
	
			.search-variants__item {
				width: 100%;
			}
		}
		.search-variants__list_item {
			margin-bottom: 15px;
		}
		.search-variants__button a {
			height: 50px;
			line-height: 50px;
		}
		.search-variants__form form p {
			font-size: 18px;
			margin-bottom: 10px;
		}
		.search-variants__form .form-item {
			margin-bottom: 10px;
		}
		.search-variants__form form {
			textarea {
				height: 70px;
			}
			input[type="text"] {
				height: 35px;
				line-height: 35px;
			}
			.button {
				height: 40px;
			}
		}
		.search-variants__list_item {
			height: auto;
		}
	}

	@include media-780 {
		&.horisontal {
			.search-variants__list {
				flex-wrap: wrap;
				padding: 0 20px;
			}
			.search-variants__list_item {
				width: 49%;
			}
			.search-variants__form {
				width: 90% !important;
			}
		} 
	}

	@include media-600 {
		&.horisontal {
			.search-variants__list {
				display: block;
			}
			.search-variants__list_item {
				width: 100%;
			}
			.search-variants__form form {
				padding: 20px;
			}
			.search-variants__button a {
				width: 90%;
			}
		} 
	}

	@include media-480 {
		h3 {
			margin-bottom: 30px;
		}
		&.horisontal {
			.search-variants__button a {
				width: 100%;
			}
			.search-variants__button {
				padding: 0 20px;
			}
		} 
	}

	@include media-420 {
		.search-variants__form .form-item {
			margin-bottom: 18px;
		  }
	}
} 

