/* slider */
.slider__content {
	box-shadow: 0 0 30px rgba(0,0,0,.3);
	border-radius: 10px;
}
.slider__item {
	border-radius: 10px;
	overflow: hidden;
	position: relative;

	.flex-block {
		align-items: center;
	}

	.button {		
		position: absolute;
		bottom: 35px;
	}
}
.slider__item__img {
	width: 60%;
	max-width: 670px;
	margin: 0 auto 0 0;
	img {
		height: 400px;
		width: auto;
		display: block;
		border-radius: 10px 0 0 10px;
	}
}

.slider__item__text {
	padding-left: 30px;
	padding-right: 20px;
	padding-top: 20px;
	width: 40%;

	p {
		padding-bottom: 70px;
	}
}
.slider-title {
	font-size: 24px;
	font-weight: 600;
	line-height: 28px;
	margin-bottom: 30px;
}

.slider__wrapper {
	padding-bottom: 55px;
}
.slider__section {
	overflow: hidden;
	.slick-list {
		overflow: hidden;
	} 
	
	.slider__content {
		min-width: 0;
		position: relative;
		height: 400px;
	}
	.slick-track{
		display: flex;
	}
	.slick-arrow{
		position: absolute;
		display: block;
		width: 40px;
		height: 40px;
		border-radius: 50px;
		background: #eceff3;
		margin: 0 5px;
		font-size: 0;
		bottom: 40px;
		z-index: 100;
		right: 100px;
		border: none;
		outline: none;
		cursor: pointer;
	
		&:before {
			content: '';
			height: 20px;
			width: 12px;
			background: url(#{$image-url}slider-arrow.png) left top no-repeat;
			position: absolute;
			top: 10px;
			left: 12px;
		}
		&:hover:before {
			background-position: left bottom;
		}
		&.slick-next {
			right: 45px;
		}
		&.slick-next:before {
			transform: rotate(180deg);
			left: 16px;
		}
	}
	
	.slick-dots {
		position: absolute;
			display: flex;
			justify-content: center;
			bottom: -50px;
			width: 100%;
			li {
				width: 20px;
				height: 20px;
				margin: 0 6px;
	
				button {
					display: block;
					width: 20px;
					height: 20px;
					border-radius: 50px;
					background: #b4bece;
					font-size: 0;
					border: none;
					outline: none;
					cursor: pointer;				
				}
				&.slick-active {
					button {
						background: #c80d0d;
					}
				}
			}
	}
} 

.slider__section {
	@include media-1100 {
		.slider__item .flex-block {
			display: block;
		}
		.slider__content {
			height: auto;
		}
		.slider__item__img {
			overflow: hidden;
			box-shadow: 0 5px 10px rgba(0,0,0,.2);
			margin-bottom: 20px;
			border-radius: 10px 10px 0 0;
			width: 100%;
			margin: 0 auto;
			img {
				display: block;
				margin-left: auto;
				margin-right: auto;
				width: 100%;
				max-width: 670px;
				border-radius: 0;
				height: auto;
			}
		}
		.slider-title {
			margin-bottom: 10px;
		}
		.slider__item .button {
			position: relative;
			bottom: auto;
		}
		.slider__item__text {
			padding-bottom: 20px;
			width: 100%;
			p {
				padding-bottom: 20px;
			}
		}
	
		.slick-arrow {
			bottom: 20px;
		}
	}
	@include media-480 {
		.slick-arrow {
			right: 65px;
		}
		.slick-arrow.slick-next {
			right: 10px;
		}
		.button_big {
			font-size: 14px;
			padding: 0 15px;
		}
	}
}


